:root {
  --black-50: rgba(0, 0, 0, 0.5);
}

.main {
  height: calc(100vh - 128px);
  background: #f5f5f5;
}

.top {
  padding: 24px 32px;
}

.topSection1 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 280px;
  margin: 0 auto;
}

.topSection2 {
  text-align: center;
  font-weight: normal;
  font-size: 24px;
  margin: 12px 0 0;
}

.oval {
  width: 56px;
  height: 56px;
  background-color: var(--black-50);
  border-radius: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.ovalHeading {
  width: 40px;
  height: 24px;
  font-family: Roboto;
  font-size: 20px;
  font-weight: 500;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: 0.25px;
  text-align: center;
  color: #ffffff;
}

.ovalCaption {
  width: 40px;
  height: 16px;
  font-family: Roboto;
  font-size: 12px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.33;
  letter-spacing: 0.4px;
  text-align: center;
  color: #ffffff;
}

.surface {
  width: 96px;
  height: 96px;
  background-position: center;
  background-size: cover;
  border-radius: 50%;
}

.bottom {
  padding: 16px;
}

.calNumber {
  margin: 8px 0;
}
.calNumber > div,
.calNumber > h3 {
  display: flex;
  justify-content: space-between;
}

.calNumber > h3 {
  height: 28px;
  font-size: 24px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  margin: 0;
}

.calNumber > div {
  height: 16px;
  font-size: 12px;
  font-weight: normal;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.33;
  letter-spacing: 0.4px;
  color: rgba(0, 0, 0, 0.6);
}
